*
*  # Utilites
*
*  Additional animations, transitions, transforms and other utilities
*
*  Version: 1.0
*  Latest update: May 25, 2015
*
* ---------------------------------------------------------------------------- */
.rotate-45 {
  transform: rotate(45deg);
}
.rotate-90 {
  transform: rotate(90deg);
}
.rotate-180 {
  transform: rotate(180deg);
}
.rotate-45-inverse {
  transform: rotate(-45deg);
}
.rotate-90-inverse {
  transform: rotate(-90deg);
}
.rotate-180-inverse {
  transform: rotate(-180deg);
}
